<template>
  <div class="warp">
    <mt-search v-model="value" :result.sync="result" placeholder="请输入站点名称"></mt-search>
    <div class="flex-box btnbox"><mt-button type="primary" size="large" @click.native="siteLocation">站点定位</mt-button><mt-button type="danger" size="large" @click.native="siteRevise">站点校正</mt-button></div>

    <div class="mainbox" style="height: calc(100vh - 55px);">

      <mt-loadmore :top-method="loadTop" :bottom-method="loadBottom" :bottom-all-loaded="allLoaded" ref="loadmore">
        <ul class="lists"  v-infinite-scroll="loadMore"
            infinite-scroll-disabled="loading"
            infinite-scroll-distance="150">
          <li v-for="(item,index) in list">
            <div class="con">
              <div class="list flex-box"><span class="label">站点名称:</span><span class="info green ">{{item.fixedReturnName}}</span></div>
              <div class="list flex-box"><span class="label">可用车辆:</span><span class="info">{{item.fixedReturnBikeNum}}</span></div>
              <!--<div class="list flex-box"><span class="label">租金:</span><span class="info">{{item.models}}</span></div>
              <div class="list flex-box"><span class="label">押金:</span><span class="info">{{item.fixLasttime}}</span></div>

              <table style="width:100%;text-align:center;">
                <thead>
                  <tr>
                    <th>租金</th>
                    <th>押金</th>
                  </tr>
                </thead>
                <tbody>
                  <tr  v-if="item.models==null">
                    <td>暂无数据</td>
                    <td>暂无数据</td>
                  </tr>
                  <tr v-for="sub in item.models" v-else>
                    <td>{{sub.modelsRentPrice}}</td>
                    <td>{{sub.modelsDeposit}}</td>
                  </tr>

                </tbody>
              </table>-->
              <div class="list flex-box"><span class="label">管理员电话:</span><span class="info">{{item.fixedReturnTel}}</span></div>
              <div class="list flex-box"><span class="label">站点介绍:</span><span class="info">{{item.fixedReturnBrief}}</span></div>
              <div class="btns">
                <mt-button @click.native="toLocation(item.fixedReturnId)" type="primary" size="small">定位</mt-button>
              </div>
            </div>
          </li>
        </ul>
      </mt-loadmore>
    </div>
  </div>
</template>
<style scoped>
.lists{padding-bottom:105px;}
.mint-search{height:auto;z-index:5}


</style>
<script>
import { Toast,Indicator,MessageBox} from 'mint-ui';
import _ from 'lodash';
    export default{
        data(){
            return{
                msg:'hello vue',
                value:'',
                result:[],
                list:[],
                allLoaded:false,
                pageIndex:1,
                totalPage:1,
                code:'',
            }
        },
        created(){


        },
        components:{

        },
        methods:{
          init(search){
            Indicator.open({
                text: '加载中...',
                spinnerType: 'fading-circle'
              });
             var _this=this;
             if(search){_this.pageIndex=1}
              $.ajax({
                url:'/api/fixedReturn/fixedInfos.action',
                data:{pageIndex:_this.pageIndex,fixedName:_this.value},
                type:'post',
                success(data){
                _this.code=data.code
                _this.totalPage=data.data.totalPage;
                Indicator.close();
                if(_this.pageIndex>_this.totalPage){
                  Toast('数据加载完毕');
                  return;
                }
                if(search){_this.list=data.data.fixedList;return}
                  _this.pageIndex++;
                  _this.list=_this.list.concat(data.data.fixedList)

                },
                error(){
                  Toast('网络连接失败')
                }
              })
          },
          loadTop(id){
            this.$refs.loadmore.onTopLoaded(id)
            this.init('')
          },
          loadBottom(id){
          this.allLoaded=true;
            this.$refs.loadmore.onBottomLoaded(id)
          },
          loadMore:_.debounce(function(){
            this.loading = true;
            this.init()
            this.loading = false;
          },100),
          siteRevise(id,index){
            MessageBox('提示', '点击校正后，请等待3分钟后刷新，查看站点定位');
            $.ajax({
              url:'/api/fixedReturn/checkBikeOfFixed.action',
              type:'post',
              success(data){
              },
              error(){
                Toast('网络连接失败')
              }
            })
          },
          siteLocation(){
            location.href='/watchFixed.html';
          },
          toLocation(fixedReturnId){
            location.href='/html/sitemap.html?fixedReturnId='+fixedReturnId;
          }

        },
        watch:{
          value:{
              deep: true,
              handler: _.debounce( function(val, oldVal) {// _.debounce 是一个通过 lodash 限制操作频率的函数。
                this.init(true)
              },
                // 这是我们为用户停止输入等待的毫秒数
                500
              )
          }
        },
        filters:{
        }
    }
</script>



