<template>
  <div class="warp">
    <div class="mainbox">
      <div class="subTit">车辆电量信息</div>
      <!--<div class="textleft">
        <mt-cell title="车辆编号" value="89757"></mt-cell>
        <mt-cell title="车辆状态" value="低电量"></mt-cell>
      </div>
      <div class="btns">
        <div class="btnsli flex-box">
          <mt-button type="primary" size="small">设为已处理</mt-button>
        </div>
        <div class="btnsli flex-box">
          <mt-button type="danger" size="small">返回</mt-button>
        </div>
      </div>-->

      <ul class="lists">
        <li>
          <div class="con">
            <div class="list flex-box"><span class="label">车辆编号:</span><span class="info green ">{{$route.params.id}}</span>
            </div>
            <div class="list flex-box"><span class="label">车辆状态:</span><span class="info">低电量</span>
            </div>

            <div class="btns">
              <div v-if="powerState==false" class="btnsli flex-box">
                <mt-button @click.native="setPower()" type="primary" size="small">设为已处理</mt-button>
              </div>
              <div v-if="powerState==true" class="btnsli flex-box">
                <mt-button type="danger" size="small">已处理</mt-button>
              </div>
              <div class="btnsli flex-box">
                <mt-button @click.native="goBack()" type="danger" size="small">返回</mt-button>
              </div>
            </div>
          </div>
        </li>
      </ul>
    </div>
  </div>
</template>
<style scoped>
.btns{margin-top:10px;}
.btns .btnsli a{-webkit-box-flex: 1;-webkit-flex: 1;flex: 1; width:100%;text-align:center;    display: -webkit-flex;
    display: flex;}
.btns .btnsli button{margin:5px;-webkit-box-flex: 1;-webkit-flex: 1;flex: 1;}
</style>
<script>
import { Toast,Indicator } from 'mint-ui';
    export default{
        data(){
            return{
              powerState:false,
            }
        },
        methods:{

          setPower(){
          var _this=this;
            $.ajax({
              url:'/api/bikeMessage/bikeAlert.action',
              data:{bikeCode:_this.$route.params.id},
              type:'post',
              success(data){
                if(data.message=='success'){
                   _this.powerState=true;
                }
              },
              error(){
                Toast('网络连接失败')
              }
            })
          },
          goBack(){
            this.$router.go(-1)
          }
        },
        components:{

        }
    }
</script>



