<template>
  <div class="warp">
    <div class="mainbox">
        <ul class="lists">
          <li>
            <div class="con">
              <div class="list flex-box"><span class="label">车辆编号:</span>{{con.bikeCode}}&nbsp;&nbsp;&nbsp;&nbsp;<mt-button v-if="con.bikeAlert==2" @click.native="goPower(con.bikeCode)" type="danger" size="small">低电量</mt-button>
                <mt-button v-if="con.bikeAlert==3" type="primary" size="small">低电量</mt-button><span class="info green "></span>
              </div>
              <div class="list flex-box"><span class="label">车辆状态:</span>{{con.bikeState | setstate}}<span class="info"></span>
              </div>
	      <div class="list flex-box"><span class="label">所属站点:</span>{{fixed.fixedReturnName}}<span class="info"></span>
              </div>
              <div class="list flex-box"><span class="label">车锁状态:</span>{{lock.bikeLockStatus | setLockState}}<span class="info"></span>
              </div>
              <div class="list flex-box"><span class="label">车锁电压:</span>{{lock.bikeLockVoltage}}V<span class="info"></span>
              </div>
              <div class="list flex-box"><span class="label">车锁在线状态:</span><span class="info local-state" :class="lock.bikeLockState==1?'local-state-on':''">{{lock.bikeLockState | setonLineState}}</span>
              </div>
              <div class="list flex-box"><span class="label">最后租赁时间:</span>{{con.bikeLastRentTime | datetime('无租赁记录')}}<span class="info"></span>
              </div>
              <div class="list flex-box" v-if="user"><span class="label">最后租赁用户账号:</span>{{user.userTel}}<span class="info"></span>
              </div>
              <div class="list flex-box"><span class="label">最后维护时间:</span>{{con.fixLasttime | datetime('无维护记录')}}<span class="info"></span>
              </div>
              <div class="btns">
                <div class="btnsli flex-box">
                  <mt-button type="primary" size="small" @click.native="gotoPage(con.bikeId,'bikeinfosearch')">信息查看</mt-button>
                  <mt-button type="primary" size="small" @click.native="gotoPage(con.bikeId,'bikeMaintainDetails')">维护操作</mt-button>
                </div>
                <div class="btnsli flex-box">
                  <mt-button type="primary" size="small" @click.native="gotoPage(con.bikeId,'bikeStateEdit')">状态修改</mt-button>
                  <mt-button @click.native="openLock(1)" type="primary" size="small">车辆开锁</mt-button>

                </div>
                <div class="btnsli flex-box">

                  <mt-button @click.native="openLock(0)" type="danger" size="small">车辆关锁</mt-button>
                  <mt-button type="primary" size="small" @click.native="gotoPage('','bike')">返回主页</mt-button>

                </div>
		 <div class="btnsli flex-box">

                  <mt-button @click.native="getBikeAlert()" type="primary" size="small">获取电压</mt-button>

                </div>
              </div>
            </div>
          </li>
        </ul>
    </div>
  </div>
</template>
<style scoped>
.mint-search{height:auto;z-index:5}
.btns .btnsli a{-webkit-box-flex: 1;-webkit-flex: 1;flex: 1; width:100%;text-align:center;    display: -webkit-flex;
    display: flex;}
.btns .btnsli button{margin:5px;-webkit-box-flex: 1;-webkit-flex: 1;flex: 1;}
.local-state{display:inline-block;position:relative; color:#999; }
.local-state.local-state-on{color:#009900;}
.local-state:before{content:'';width:20px; height:20px; background:#999;  border-radius: 50%;position: absolute;left:-27px; top:0;}
.local-state.local-state-on:before{ background:#009900;}

</style>
<script>
import { Toast,Indicator } from 'mint-ui';
    export default{
        data(){
            return{
                con:'',
                user:'',
                lock:'',
		fixed:'',

            }
        },
        created(){
          this.init()

        },
        components:{

        },
        methods:{
          init(){
         var _this=this;
          $.ajax({
            url:'/api/bikeMessage/bikeMessage.action',
            data:{bikeCode:_this.$route.params.id},
            type:'post',
            success(data){
                _this.con=data.data.bike;
                _this.user=data.data.user;
                _this.lock=data.data.bikeLockInfo;
		_this.fixed=data.data.fixedReturn;
            },
            error(){
              Toast('网络连接失败')
            }
          })
          },
          goPower(id){
            this.$router.push({ path: '/power/'+id })
          },
          gotoPage(id,page){
            location.href="#/"+page+'/'+id
          },
          openLock(flag){
          var _this=this;
            $.ajax({
              url:'/api/bikeMessage/useLock.action',
              data:{bikeCode:_this.con.bikeCode,flag:flag},
              type:'post',
              success(data){
              if(data.data.success=='success'){
                if(data.data.code==0){
                  Toast('发送失败')
                }else if(data.data.code==1){
                  Toast('关锁成功')
                }else if(data.data.code==2){
                  Toast('开锁成功')
                }
              }

              },
              error(){
                Toast('网络连接失败')
              }
            })
          },
	  getBikeAlert(){
          var _this=this;
            $.ajax({
              url:'/api/bikeMessage/getBikeAlert.action',
              data:{bikeCode:_this.con.bikeCode},
              type:'post',
              success(data){
              if(data.message=='success'){
	          _this.con=data.data.nowBike;
                  Toast('获取成功')
              }

              },
              error(){
                Toast('网络连接失败')
              }
            })
          }

        },
        filters:{
          setLockState(val){
             var text='';
             switch(val){
                case 0:
                  text= '锁上';
                  break;
                case 1:
                  text= '未锁';
                  break;
                case 2:
                  text= '受阻';
                  break;
                default:
                  text='未知'
             }
             return text
          },
          setonLineState(val){
          var text='';
             switch(val){
                case 0:
                  text='未在线';
                  break;
                case 1:
                  text= '在线';
                  break;
                default:
                  text='未知'
             }
             return text
          }
        }
    }

</script>



