<template>
  <div class="warp">
    <div class="mainbox">
      <mt-radio
        title="请选择渠道"
        v-model="value"
        :options="options ">
      </mt-radio>
    </div>
  </div>
</template>
<style scoped>
.lists{padding-bottom:105px;}
.mint-search{height:auto;z-index:5}


</style>
<script>
const wx=require('weixin-js-sdk');
import { Radio,Indicator,Toast,MessageBox } from 'mint-ui';
    export default{
        data(){
            return{
              value:null,
              options : null
              }
        },
        created(){
          var _this=this;
          $.ajax({
            url:'/api/admin/getAdmin.action',
            async: false,
            type:'post',
            success(data){
                var channerls=data.data.channels,arr=[];
                for(var i=0;i<channerls.length;i++){
                  var obj={};
                  obj.label=channerls[i].channelName;
                  obj.value=String(channerls[i].channelId);
                  arr.push(obj)
                }

                _this.options =arr
            },
            error(){
              Toast('网络连接失败')
            }
          })

        },
        components:{

        },
        watch:{
          value(){
            this.getLocation(this.value)
          }
        },
        methods:{
            getLocation(channleid){
              var hidstr = "url="+encodeURIComponent(window.location.href.split('#')[0]);
               MessageBox.confirm('将获取你当前手机定位').then(action => {
                  $.ajax({
                     url : "/api/bikeMessage/scanCode.action",
                     data:hidstr,
                     async : true,
                     type : "post",
                     dataType:"json",
                     success : function(data) {
                       wx.config({
                            debug: false,
                            appId: data.appId,
                            timestamp: data.timestamp,
                            nonceStr: data.nonceStr,
                            signature: data.signature,
                            jsApiList: [
                              'getLocation'
                            ]
                        });
                       wx.ready(function () {
                         wx.getLocation({
                            type: 'gcj02', // 默认为wgs84的gps坐标，如果要返回直接给openLocation用的火星坐标，可传入'gcj02'
                            success: function (res) {
                              var latitude = res.latitude; // 纬度，浮点数，范围为90 ~ -90
                              var longitude = res.longitude; // 经度，浮点数，范围为180 ~ -180。
                              var speed = res.speed; // 速度，以米/每秒计
                              var accuracy = res.accuracy; // 位置精度
                               $.ajax({
                                   url : "/api/fixedReturn/addFixed.action",
                                   type : "post",
                                   data :{fixedReturnLat:latitude,fixedReturnLng:longitude,fixedReturnOriginatorId:global.adminId,fixedReturnChannelId:channleid},
                                   dataType:"json",
                                   success : function(data) {
				   if(data.message=="fail"){
                                        Toast('定位获取失败')
					location.href='/#/bike';
				   }else{
                                        Toast('提交成功')
                                      location.href='/#/bike';
				   }
                                     
                                   }
                                })

                            }
                        });
                       })

                     },
                     error : function() {
                     }
                  });
               });

            }

        },
        filters:{
        }
    }
</script>



