<template>
  <div class="warp">
    <mt-search v-model="value" :result.sync="result"></mt-search>
    <div class="flex-box btnbox"><mt-button type="primary" size="large" @click.native="bikeLocation">车辆定位</mt-button><mt-button type="danger" size="large" @click.native="siteRevise">站点校正</mt-button></div>

    <div class="mainbox" style="height: calc(100vh - 55px);">

      <mt-loadmore :top-method="loadTop" :bottom-method="loadBottom" :bottom-all-loaded="allLoaded" ref="loadmore">
        <ul class="lists" v-infinite-scroll="loadMore"
            infinite-scroll-disabled="loading"
            infinite-scroll-distance="150">
          <li v-for="item in list">
            <div class="tit flex-box">
              <h2>车辆编号：{{item.bikeCode}}</h2>
              <div v-if="item.lockState==1" class="local-state local-state-on">在线</div>
              <div v-else class="local-state">未在线</div>
              <router-link v-if="item.bikeAlert==2" v-bind:to="item.bikeCode | link('power')">
                <mt-button type="danger" size="small">低电量</mt-button>
              </router-link>
              <mt-button v-if="item.bikeAlert==3" type="primary" size="small">低电量</mt-button>

            </div>
            <div class="con">
              <div class="list flex-box"><span class="label">车辆状态:</span><span class="info green ">{{item.bikeState | setstate}}</span>
              </div>
              <div class="list flex-box"><span class="label">最后租赁时间:</span><span class="info">{{item.bikeLastRentTime | datetime('无租赁记录')}}</span>
              </div>
              <div class="btns">
                <mt-button @click.native="tobike(item.bikeId)" type="primary" size="small">定位</mt-button>
                <router-link v-bind:to="item.bikeId | link('bikeinfosearch')">
                  <mt-button type="primary" size="small">详情</mt-button>
                </router-link>

                <router-link v-bind:to="item.bikeId | link('bikeStateEdit')">
                  <mt-button type="danger" size="small">状态修改</mt-button>
                </router-link>

              </div>
            </div>
          </li>
        </ul>
      </mt-loadmore>
    </div>
  </div>
</template>
<style scoped>
.mint-search{height:auto;z-index:5}
.lists{padding-bottom:44px;}
.local-state{display:inline-block; width:50px;position:relative; color:#999; }
.local-state.local-state-on{color:#009900;}
.local-state:before{content:'';width:20px; height:20px; background:#999;  border-radius: 50%;position: absolute;left:-27px; top:0;}
.local-state.local-state-on:before{ background:#009900;}

</style>
<script>
import { Toast,Indicator,MessageBox } from 'mint-ui';
import _ from 'lodash';
    export default{
        data(){
            return{
                msg:'hello vue',
                value:'',
                result:[],
                list:[],
                allLoaded:false,
                pageIndex:1,
                totalPage:100,
            }
        },
        created(){


        },
        components:{

        },
        methods:{
          init(search){
            Indicator.open({
                text: '加载中...',
                spinnerType: 'fading-circle'
              });
             var _this=this;
             if(search){_this.pageIndex=1}
              $.ajax({
                url:'/api/bikeMessage/bikeRentDetailSearch.action',
                data:{pageIndex:_this.pageIndex,bikeCode:_this.value},
                type:'post',
                success(data){
                _this.totalPage=data.data.totalPage;
                Indicator.close();

                if(_this.pageIndex>_this.totalPage && _this.totalPage!=0){
                  Toast('数据加载完毕');
                  return;
                }
                if(search){_this.list=data.data.bikeList;return}

                  _this.pageIndex++;
                  _this.list=_this.list.concat(data.data.bikeList)

                },
                error(){
                  Toast('网络连接失败')
                }
              })
          },
          loadTop(id){
              this.$refs.loadmore.onTopLoaded(id)
              console.log(this.$refs.loadmore)
            },
            loadBottom(id){
            this.allLoaded=true;
              this.$refs.loadmore.onBottomLoaded(id)
            },
          loadMore:_.debounce(function(){
            this.loading = true;
            this.init()
            this.loading = false;
          },100),
          tobike(id){
            location.href='/html/map.html?bikeid='+id
          },
          siteRevise(id,index){
            MessageBox('提示', '点击校正后，请等待3分钟后刷新，查看站点定位');
            $.ajax({
              url:'/api/fixedReturn/checkBikeOfFixed.action',
              type:'post',
              success(data){
              },
              error(){
                Toast('网络连接失败')
              }
            })
          },
          bikeLocation(){
            location.href='/watchbike.html';
          }

        },
        watch:{
          value:{
              deep: true,
              handler: _.debounce( function(val, oldVal) {// _.debounce 是一个通过 lodash 限制操作频率的函数。
                this.init(true)
              },
                // 这是我们为用户停止输入等待的毫秒数
                500
              )
          }
        },
        filters:{
        }
    }



</script>



