<template>
  <div class="warp">
    <div class="mainbox">
      <mt-loadmore :top-method="loadTop" :bottom-method="loadBottom" :bottom-all-loaded="allLoaded" ref="loadmore">
      <ul class="lists" v-infinite-scroll="loadMore"
          infinite-scroll-disabled="loading"
          infinite-scroll-distance="150">
        <li >
          <div class="tit flex-box"><h2>车辆编号：{{bikeCode}}</h2></div>
          <div class="con">
            <table class="g-table">
              <thead><tr><td>维护批次</td><td>维护开始</td><td>完成时间</td></tr></thead>
              <tbody>
              <tr v-for="item in list"><td>{{item.fixBatchNumber}}</td><td>{{item.fixStarttime | datetime}}</td><td>{{item.fixEndtime | datetime('维护中')}}</td></tr>

              </tbody>
            </table>
          </div>
        </li>
      </ul>
      </mt-loadmore>
    </div>
  </div>
</template>
<style scoped>
  .textleft{text-align:left;}
  .bikeinfomain{flex-wrap: wrap; margin:3%;}
  .bikeinfomain button{width: 46%; margin:  2%;}
  .lists .btns button{margin:0px;}
</style>
<script>
import { Toast,Indicator } from 'mint-ui';
import _ from 'lodash';
    export default{
        data(){
            return{
                msg:'hello vue',
                value:'',
                result:['jou','chou','joubn'],
                list:[],
                allLoaded:false,
                pageIndex:1,
                totalPage:100,
                code:'',
                bikeCode:null,
            }
        },
        created(){


        },
        components:{

        },
        methods:{
          init(){
            Indicator.open({
                text: '加载中...',
                spinnerType: 'fading-circle'
              });
             var _this=this;
              $.ajax({
                url:'/api/bikeMessage/fixInfoList.action',
                data:{pageIndex:_this.pageIndex,bikeId:_this.$route.params.id},
                type:'post',
                success(data){
                Indicator.close();
                 _this.code=data.code
                _this.totalPage=data.data.totalPage;;
                 _this.bikeCode=data.data.bikeCode
                if(_this.pageIndex>_this.totalPage){
                  Toast('数据加载完毕');
                  return;
                }
                  _this.pageIndex++;
                  _this.list=_this.list.concat(data.data.bikeFixInfoList)

                },
                error(){
                  Toast('网络连接失败')
                }
              })
          },
          loadTop(id){
              this.$refs.loadmore.onTopLoaded(id)
              console.log(this.$refs.loadmore)
            },
            loadBottom(id){
            this.allLoaded=true;
              this.$refs.loadmore.onBottomLoaded(id)
            },
          loadMore:_.debounce(function(){
            this.loading = true;
            this.init()
            this.loading = false;
          },100),


        },
        filters:{
        }
    }
</script>



