<template>
  <div class="warp">
    <div class="flex-box btnbox">
      <select v-model="state" style="width: 100%;height:30px;" @change="gostate">
        <option :value="null">请选择状态</option>
        <option value="2">未审核</option>
        <option value="1">已通过</option>
        <option value="3">已驳回</option>
      </select>

    </div>

    <div class="mainbox">
      <mt-loadmore :top-method="loadTop" :bottom-method="loadBottom" :bottom-all-loaded="allLoaded" ref="loadmore">
        <ul class="lists"  v-infinite-scroll="loadMore"
            infinite-scroll-disabled="loading"
            infinite-scroll-distance="150">
          <li v-for="(item,index) in list">
            <div class="con">
              <div class="list flex-box"><span class="label">状态:</span><span class="info">{{item.fixedReturnState | setstate}}</span></div>
              <div class="list flex-box"><span class="label">定位时间:</span><span class="info">{{item.fixedReturnFixedTime | datetime('无定位时间')}}</span></div>
              <div class="list flex-box"><span class="label">经纬度:</span><span class="info">{{item.fixedReturnLat}} , {{item.fixedReturnLng}}</span></div>
              <div class="btns">
                <mt-button @click="toLocation(item.fixedReturnLat,item.fixedReturnLng)" type="primary" size="small">定位</mt-button>
              </div>
            </div>
          </li>
        </ul>
      </mt-loadmore>
    </div>
  </div>
</template>
<style scoped>
.lists{padding-bottom:105px;}
.mint-search{height:auto;z-index:5}


</style>
<script>
const wx=require('weixin-js-sdk');
import { Toast,Indicator,MessageBox} from 'mint-ui';
import _ from 'lodash';
    export default{
        data(){
            return{
                state:null,
                msg:'hello vue',
                value:'',
                result:[],
                list:[],
                allLoaded:false,
                pageIndex:1,
                totalPage:1,
                code:'',
            }
        },
        created(){


        },
        components:{

        },
        methods:{
          init(search){

             var _this=this;
             if(search){_this.pageIndex=1}
              $.ajax({
                url:'/api/fixedReturn/myFixedList.action',
                data:{pageIndex:_this.pageIndex,fixedState:_this.state},
                type:'post',
                cache:false,
                success(data){
                _this.code=data.code
                _this.totalPage=data.data.totalPage;

                if(_this.pageIndex>_this.totalPage && _this.totalPage!=0){
                  Toast('数据加载完毕');
                  return;
                }
                if(search){_this.list=data.data.fixedList;return}
                  _this.pageIndex++;
                  _this.list=_this.list.concat(data.data.fixedList)

                },
                error(){
                  Toast('网络连接失败')
                }
              })
          },
          loadTop(id){
            this.$refs.loadmore.onTopLoaded(id)
            this.init(true)
          },
          loadBottom(id){
          this.allLoaded=true;
            this.$refs.loadmore.onBottomLoaded(id)
          },
          loadMore:_.debounce(function(){
            this.loading = true;
            this.init()
            this.loading = false;
          },100),
          siteRevise(id,index){
            MessageBox('提示', '点击校正后，请等待3分钟后刷新，查看站点定位');
            $.ajax({
              url:'/api/fixedReturn/checkBikeOfFixed.action',
              type:'post',
              success(data){
              },
              error(){
                Toast('网络连接失败')
              }
            })
          },
          siteLocation(){
            location.href='/watchFixed.html';
          },
          toLocation(fixedReturnId){
            location.href='/html/sitemap.html?fixedReturnId='+fixedReturnId;
          },
          gostate(){
            this.init(true)
          },
          toLocation(lat,lng){
            var hidstr = "url="+encodeURIComponent(window.location.href.split('#')[0]);
            $.ajax({
                 url : "/api/bikeMessage/scanCode.action",
                 data:hidstr,
                 async : true,
                 type : "post",
                 dataType:"json",
                 success : function(data) {
                   wx.config({
                        debug: false,
                        appId: data.appId,
                        timestamp: data.timestamp,
                        nonceStr: data.nonceStr,
                        signature: data.signature,
                        jsApiList: [
                          'openLocation'
                        ]
                    });
                   wx.ready(function () {
                     wx.openLocation({
                        latitude: lat, // 纬度，浮点数，范围为90 ~ -90
                        longitude: lng, // 经度，浮点数，范围为180 ~ -180。
                        name: '', // 位置名
                        address: '', // 地址详情说明
                        scale: 17, // 地图缩放级别,整形值,范围从1~28。默认为最大
                        infoUrl: '' // 在查看位置界面底部显示的超链接,可点击跳转
                    });
                   })

                 },
                 error : function() {
                 }
              });
          }

        },
        filters:{
          setstate(val){
            switch(val){
              case 1:
                var text='已通过';
                break;
              case 2:
                var text='未审核';
                break;
              case 3:
                var text='已驳回';
                break;
              default:
                var text='未审核';
                break
             }
             return text

          }
        }
    }
</script>



