<template>
  <div class="warp">
    <div class="mainbox dtbg">
      <div class="messtop flex-box"><i class="icon-notificationfill"></i>您有新的反馈信息及时处理</div>
      <div class="bikemain flex-box">
        <router-link to="bikeinfosearch"><mt-button type="primary" size="large">车辆信息</mt-button></router-link>
        <router-link to="bikefeedbackinfo"><mt-button type="primary" size="large">反馈信息</mt-button></router-link>
        <router-link to="bikemaintaininfo"><mt-button type="primary" size="large">车辆维护</mt-button></router-link>
        <router-link to="siteLocation"><mt-button type="primary" size="large">站点信息</mt-button></router-link>
        <router-link to="insurance"><mt-button type="primary" size="large">保险申请</mt-button></router-link>
        <a @click="setLoaction"><mt-button type="primary" size="large">站点设置</mt-button></a>
        <router-link to="locationList"><mt-button type="primary" size="large">站点列表</mt-button></router-link>
      </div>
      <mt-button type="danger" class="sys" @click.native="scancode">扫一扫</mt-button>
    </div>


  </div>
</template>

<style scoped>
.sys{position:absolute;width:100px; left:50%;bottom:20px; margin-left:-50px;    border-radius: 23px;}
</style>
<script>
  const wx=require('weixin-js-sdk');
  import { Radio,Indicator,Toast,MessageBox  } from 'mint-ui';
    export default{
        data(){
            return{
                msg:'hello vue'
            }
        },
        methods:{
          tomain(){
            location.href="/watchbike.html";
          },
          scancode(){
            var hidstr = "url="+encodeURIComponent(window.location.href.split('#')[0]);
		    			   $.ajax({
		    			       url : "/api/bikeMessage/scanCode.action",
		    			       data:hidstr,
		    			       async : true,
		    			       type : "post",
		    			       dataType:"json",
		    			       success : function(data) {
		    			    	   wx.config({
		    			    		      debug: false,
		    			    		      appId: data.appId,
		    			    		      timestamp: data.timestamp,
		    			    		      nonceStr: data.nonceStr,
		    			    		      signature: data.signature,
		    			    		      jsApiList: [
		    			    		        'scanQRCode'
		    			    		      ]
		    			    		  });
		    			    	   wx.ready(function () {
		    				    	   wx.scanQRCode({
		    				    		    needResult: 1, // 默认为0，扫描结果由微信处理，1则直接返回扫描结果，
		    				    		    scanType: ["qrCode","barCode"], // 可以指定扫二维码还是一维码，默认二者都有
		    				    		    success: function (res) {
		    				    		    var result = res.resultStr; // 当needResult 为 1 时，扫码返回的结果
		    				    		    var results=[];
		    				    		    results=result.split("?bike=",2);
		    				    		    if(results[1]!=undefined){
		    				    		      location.href="/#/bikeInformation/"+results[1];
		    				    		    }else{
		    				    		    	Toast("您扫码的二维码有误，请您选择正确的二维码");

		    				    		    }
		    				    		}
		    				    		});
		    			    	   })

		    			       },
		    			       error : function() {
		    			       }
		    			   });
          },
          setLoaction(){
             if(global.channelsId==null){
                location.href='/#/selectqudao'
             }else{
              this.getLocation(global.channelsId)
             }
          },
          getLocation(channleid){
              var hidstr = "url="+encodeURIComponent(window.location.href.split('#')[0]);
               MessageBox.confirm('将获取你当前手机定位').then(action => {
                  $.ajax({
                     url : "/api/bikeMessage/scanCode.action",
                     data:hidstr,
                     async : true,
                     type : "post",
                     dataType:"json",
                     success : function(data) {
                       wx.config({
                            debug: false,
                            appId: data.appId,
                            timestamp: data.timestamp,
                            nonceStr: data.nonceStr,
                            signature: data.signature,
                            jsApiList: [
                              'getLocation'
                            ]
                        });
                       wx.ready(function () {
                         wx.getLocation({
                            type: 'gcj02', // 默认为wgs84的gps坐标，如果要返回直接给openLocation用的火星坐标，可传入'gcj02'
                            success: function (res) {
                              var latitude = res.latitude; // 纬度，浮点数，范围为90 ~ -90
                              var longitude = res.longitude; // 经度，浮点数，范围为180 ~ -180。
                              var speed = res.speed; // 速度，以米/每秒计
                              var accuracy = res.accuracy; // 位置精度
                               $.ajax({
                                   url : "/api/fixedReturn/addFixed.action",
                                   type : "post",
                                   data :{fixedReturnLat:latitude,fixedReturnLng:longitude,fixedReturnOriginatorId:global.adminId,fixedReturnChannelId:channleid},
                                   dataType:"json",
                                   success : function(data) {
                                     Toast('提交成功')
                                     location.href='/#/bike';
                                   }
                                })

                            }
                        });
                       })

                     },
                     error : function() {
                     }
                  });
               });

            }
        },
        components:{

        }
    }
</script>



